<template>
  <div class="container p-4">
    <form @submit.prevent="submit">
      <div class="card m-4">
        <div class="card-header">Mon Espace</div>
        <div class="card-body">
          <div class="text-center text-warning" v-if="errorMessage">
            {{ errorMessage }}
            <hr />
            <button class="btn btn-block btn-warning" @click="tray_again" type="button">Réessayer</button>
          </div>
          <loading-compoenent v-else-if="isLoading" />
          <div v-else>
            <div class="form-group py-1">
              <input type="email" v-model="user.email" class="form-control" placeholder="Email *" />
            </div>
            <div class="form-group py-1">
              <input
                type="password"
                v-model="user.password"
                class="form-control"
                placeholder="Password *"
              />
            </div>
          </div>
        </div>
        <div class="card-footer" v-if="!errorMessage && !isLoading">
          <div class="d-grid p-1">
            <button class="btn btn-primary btn-lg" type="submit">
              <i class="bi-person me-2" /> Entrer
            </button>
          </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import { auth } from "../firebase";

export default {
  name: "inscriptionDistributeurComponent",
  components: {},
  data() {
    return {
      user: {
        email: "7mmdmo7@gmail.com",
        password: "7mmdmo7"
      },
      errorMessage: null,
      isLoading: false
    };
  },
  created() {},
  methods: {
    tray_again() {
      this.isLoading = false;
      this.errorMessage = null;
    },
    submit() {
      event.preventDefault();
      this.isLoading = true;
      this.errorMessage = null;
      auth
        .signInWithEmailAndPassword(this.user.email, this.user.password)
        .then(() => {
          this.isLoading = false;
          this.errorMessage = null;
          /*this.$router.push({
            name: "home"
          });*/
        })
        .catch(error => {
          this.isLoading = false;
          this.errorMessage = error;
          console.log("error", error);
          this.$swal({
            title: "Ouaps!",
            text: error.message,
            icon: "error",
            confirmButtonText: "Tray again"
          });
        });
    }
  }
};
</script>

